.about-page {
  text-align: center;
  padding: 50px;
  background-color: #f4f4f4;
}

/* üìå Back-EndÏôÄ Front-EndÎ•º ÎÇòÎûÄÌûà Î∞∞Ïπò */
.skills-container {
  display: flex
  ;
      justify-content: center;
      align-items: flex-start;
      gap: 102px;
      width: 100%;
      max-width: 1600px; 
      margin: auto;
}

.backend-container, .frontend-container {
  width: 50%;
}

/* üìå ÌÉÄÏù¥ÌãÄ Ïä§ÌÉÄÏùº */
.about-page-h3 {
  font-size: 1.5rem;
  color: #555;
  margin-bottom: 10px;
  text-align: center;
}

/* üìå Î¶¨Ïä§Ìä∏Î•º 2Ïó¥Î°ú Î∞∞Ïπò */
.about-page-ul {
  list-style-type: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* üìå 2Ïó¥ Î∞∞Ïπò */
  gap: 42px;
  width: 100%;
  justify-items: center;
}

/* üìå Í∞Å ÏïÑÏù¥ÌÖúÏùÑ ÎèôÏùºÌïú ÌÅ¨Í∏∞Î°ú Î∞∞Ïπò */
.about-page-li {
  position: relative;
  background-color: white;
  border: 2px solid #ddd;
  padding: 15px;
  border-radius: 8px;
  font-size: 1.2rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease-in-out;
  cursor: pointer;
  width: 100%; /* üìå Î∂ÄÎ™®(grid)Ïóê ÎßûÏ∂§ */
  min-width: 150px;
  height: 120px;
}

/* üìå ÎßàÏö∞Ïä§ Ïò§Î≤Ñ Ìö®Í≥º */
.about-page-li:hover {
  background-color: #6dd170;
  color: white;
  font-size: 17px;
  transition: 0.3s;
}

/* üìå Î∞òÏùëÌòï: ÌÉúÎ∏îÎ¶øÏóêÏÑúÎäî Ìïú Ï§ÑÏóê 1Í∞úÏî© */
@media (max-width: 1024px) {
  .skills-container {
    flex-direction: column;
    align-items: center;
  }

  .backend-container, .frontend-container {
    width: 100%;
  }

  .about-page-ul {
    grid-template-columns: repeat(2, 1fr); /* üìå ÌÉúÎ∏îÎ¶øÏóêÏÑúÎèÑ 2Í∞ú Ïú†ÏßÄ */
  }
}

/* üìå Î™®Î∞îÏùºÏóêÏÑúÎäî 1Ïó¥ Î∞∞Ïπò */
@media (max-width: 768px) {
  .about-page-ul {
    grid-template-columns: repeat(1, 1fr);
  }
}

.AboutPage-Icons{
  margin-left: 10px;
  font-size: 31px;
}
