/* Overall page styling */
.homepage {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 100vh;
  background-color: #f5f5f5; /* Softer background */
  font-family: 'Arial', sans-serif;
  color: #2c3e50; /* Darker text color */
  padding: 20px;
}

/* Profile image styling */
.homepage-img {
  width: 220px;
  height: 220px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 20px;
  position: relative;
  transition: transform 0.3s ease;
}

.homepage-img:hover {
  transform: scale(1.1); /* Slight zoom effect on hover */
}

/* Text container styling */
.text-container {
  text-align: left;
  padding-left: 20px;
  max-width: 70%;
}

/* Home page title styling */
.home-page-title {
  font-size: 3rem;
  font-weight: bold;
  color: #1abc9c;
  margin-bottom: 15px;
  text-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
}

/* Speech bubble styling */
.intro {
  font-size: 1.5rem;
  line-height: 1.8;
  color: #34495e;
  background-color: #ecf0f1;
  padding: 35px;
  border-radius: 20px;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.15);
  position: relative;
  width: 80%;
  margin-bottom: 30px;
}

/* Creating the speech bubble effect */
.intro::after {
  content: '';
  position: absolute;
  bottom: -25px;
  left: 40px;
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-top: 25px solid #ecf0f1;
}

/* Hover effect for the title */
.homepage h4:hover {
  color: #16a085;
  cursor: pointer;
  transform: translateY(-5px); /* Slight upward movement */
}

/* Adding smooth scroll behavior to the whole container */
.homepage-container {
  scroll-behavior: smooth;
}

/* Smooth scrolling for buttons and general links */
.scroll-button {
  margin-top: 20px;
  padding: 12px 30px;
  font-size: 18px;
  background-color: #1abc9c;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.scroll-button:hover {
  background-color: #45a049;
  transform: scale(1.1); /* Slight zoom effect on hover */
}
