/* Container for the whole About section */
.container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: left;
  padding: 50px;
  max-width: 1200px;
  margin: auto;
  background: #f0f3f4; /* Soft, light background */
  border-radius: 30px;
  box-shadow: 8px 8px 20px rgba(0, 0, 0, 0.1); /* Lighter shadow for soft appearance */
  height: 600px;
  position: relative;
  overflow: hidden;
}

/* Rounded profile image styling */
.myimage1 {
  width: 300px;
  height: 320px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 30px;
  transition: transform 0.3s ease;
}

.myimage1:hover {
  transform: scale(1.1); /* Add zoom effect on hover */
}

/* Left side: Name and Profile Image */
.left-img-and-name {
  margin-right: 40px;
}

.title-name {
  font-size: 40px;
  font-weight: bold;
  color: #2c3e50;
  margin-bottom: 10px;
  text-shadow: 2px 2px 6px rgba(0, 0, 0, 0.1);
}

.sub-title {
  font-size: 28px;
  color: #95a5a6;
  margin-bottom: 20px;
}

/* Text and Profile Info */
.just-profile-text {
  font-size: 18px;
  color: #34495e;
  margin: 10px 0;
}

.just-profile-text-last {
  font-size: 18px;
  color: #34495e;
  margin: 10px 0;
  margin-bottom: 40px;
  display: flex;
  align-items: center;
}

.just-profile-text-last a {
  text-decoration: none;
  color: inherit;
}

.just-profile-text-last:hover {
  color: #3498db; /* Blue on hover */
}

/* Icons for social links */
.about-page-icons {
  margin-right: 10px;
  color: #1abc9c;
  transition: color 0.3s ease;
}

.about-page-icons:hover {
  color: #16a085; /* Darker green for hover effect */
}

/* Section for experience */
.my-experience-texts {
  font-size: 24px;
  font-weight: bold;
  margin-top: 40px;
  color: #2c3e50;
}

.just-profile-text22 {
  font-size: 18px;
  color: #7f8c8d;
  margin-bottom: 20px;
}

/* Fun, decorative icon */
.fun-icon {
  margin-top: 30px;
  animation: bounce 2s infinite ease-in-out;
}

/* Bounce effect for smiley icon */
@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

/* Add subtle gradient to the background */
.container {
  background: linear-gradient(45deg, #f0f3f4, #ecf0f1);
}
