.project-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

h2 {
  font-size: 24px;
  margin-bottom: 20px;
}

.project-card {
  display: flex;
  width: 90%;
  background: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 50px;
  overflow: hidden;
}

.project-images {
  display: flex;
  flex-direction: column; 
  width: 30%;
  align-items: center;
  padding: 10px;
  grid-gap: 25px;
}

.dingdong-App-images{
  /* display: flex; */
  flex-direction: row;
  /* width: 80%;
  align-items: center; */
}

.project-img-App{
  height: 190px;
}

.project-img, .project-img2 {
  width: 100%; /* Î∂ÄÎ™® ÎÑàÎπÑÏóê ÎßûÏ∂§ */
  height: auto;
  max-height: 200px; /* Ïù¥ÎØ∏ÏßÄ ÏµúÎåÄ ÎÜíÏù¥ ÏÑ§Ï†ï */
  object-fit: contain;
}

.project-img {
  width: 118%;
  height: 141px;
}

.project-img2{
  width: 98%;
  height: 270px;
  object-fit: cover;
}

.project-content {
  padding: 20px;
  flex: 1;
}

.project-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
}

.project-detail {
  font-size: 14px;
  color: grey;
  margin-bottom: 10px;
}

ul {
  padding-left: 20px;
}

.WeworkHome{
  width: 50px;
}

.project-page-button{

}

.project-page-button {
  margin-top: 20px;
  padding: 12px 30px;
  font-size: 18px;
  background-color: #1abc9c;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  margin-right: 10px;
}

.project-page-button:hover {
  background-color: #45a049;
  transform: scale(1.1); /* Slight zoom effect on hover */
}